<template>
    <div>
        <top-nav title="库存日志列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>这里是库存日志列表</p>
        </div>
        <div class="wyu-main" v-loading="loading">
            <div class="wyu-block">
                <el-form label-width="80px" size="mini" v-model="searchForm" class="search-form" :inline="true">

                    <el-form-item label="商品ID" label-width="100">
                        <el-input v-model="searchForm.goods_id"></el-input>
                    </el-form-item>

                    <el-form-item label="订单ID" label-width="100">
                        <el-input v-model="searchForm.order_id"></el-input>
                    </el-form-item>

                    <el-form-item label="商品名称" label-width="100">
                        <el-input v-model="searchForm.goods_name"></el-input>
                    </el-form-item>

                    <el-form-item label="商品码" label-width="100">
                        <el-input v-model="searchForm.goods_sn"></el-input>
                    </el-form-item>

                    <el-form-item label="规格码" label-width="100">
                        <el-input v-model="searchForm.bar_code"></el-input>
                    </el-form-item>

                    <el-form-item label="规格" label-width="100">
                        <el-input v-model="searchForm.key_name"></el-input>
                    </el-form-item>

                    <el-form-item label="类型">
                        <el-select v-model="searchForm.change_type" placeholder="请选择">
                            <el-option :key="-999" label="请选择" :value=-999></el-option>
                            <el-option v-for="(item,key) of manageType" :key="key" :label="item"
                                       :value="key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="操作时间">
                        <el-date-picker v-model="searchForm.bg_add_time" type="datetime"
                                        placeholder="选择日期时间"></el-date-picker>
                        至
                        <el-date-picker v-model="searchForm.end_add_time" type="datetime"
                                        placeholder="选择日期时间"></el-date-picker>
                    </el-form-item>

                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getLogList(1)" size="mini" type="primary">搜索
                        </el-button>
                        <a href="javascript:void(0);" @click="searchForm.goods_name = '', searchForm.key = ''
             , searchForm.goods_id = '', searchForm.order_id = '', searchForm.change_type = -999,
            searchForm.bg_add_time = '',searchForm.end_add_time = '',searchForm.bar_code = '',searchForm.goods_sn = '',getLogList()">取消搜索</a>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" size="mini">
                    <el-table-column prop="goods_id" label="商品ID" width="80"></el-table-column>

                    <el-table-column prop="order_id" label="订单ID" width="80"></el-table-column>

                    <el-table-column label="编码" width="200">
                        <template slot-scope="scope">
                            <p>商品码: {{scope.row.goods_sn}}</p>
                            <p>规格码: {{scope.row.bar_code}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="商品名称">
                        <template slot-scope="scope">
                            <div class="ellipsis-03">
                                {{scope.row.goods_name}}
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column label="缩略图" width="100">
                        <template slot-scope="scope">
                            <img :src="scope.row.photo" width="50"/>
                        </template>
                    </el-table-column>

                    <el-table-column label="规格">
                        <template slot-scope="scope">
                            <p v-if="scope.row.spec_key == ''">无规格</p>
                            <p v-if="scope.row.spec_key != ''" v-for="item in scope.row.key_name">{{item}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作数量" width="100">
                        <template slot-scope="scope">
                            <b style="color: #D94854" v-if="scope.row.change_type < 10">- {{scope.row.stock}}</b>
                            <b style="color: #5daf34;"
                               v-if="scope.row.change_type > 10 && scope.row.change_type <= 20 ">+
                                {{scope.row.stock}}</b>
                        </template>
                    </el-table-column>

                    <el-table-column prop="this_stock" label="剩余数量" width="100"></el-table-column>

                    <el-table-column prop="change_type_mean" label="操作类型" width="100"></el-table-column>

                    <el-table-column label="操作信息" width="220">
                        <template slot-scope="scope">
                            <p>操作时间：{{scope.row.add_time}}</p>
                            <p>操作IP：{{scope.row.add_ip}}</p>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page">
                    <div class="page-tool"></div>
                    <div class="page-mian">
                        <el-pagination @current-change="getLogList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: 'commentIndex',
        components: {TopNav},
        data() {
            return {
                dialogTableVisible: false,
                //整体列表 菊花
                loading: false,
                //列表数据 包含 list count limit
                datas: [],
                //修改 添加 form

                //搜索 form
                searchForm: {
                    title: '',  // 手机号码
                    is_show: -999,
                    end_add_time: '',
                    bg_add_time: '',
                    bar_code: '',
                    goods_sn: '',
                },
                commentId: 0,
                commentIds: [],
                cats: [],
                showP: false,
                img: [],
                manageType: {},
            }
        },
        created() {
            this.getLogList();
        },
        methods: {
            /**
             * 获取库存日志列表
             * @param page {number} 页数
             */
            getLogList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.goods.stock/index", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                    goods_name: this.searchForm.goods_name,
                    goods_spec: this.searchForm.goods_spec,
                    goods_sn: this.searchForm.goods_sn,
                    bar_code: this.searchForm.bar_code,
                    order_id: this.searchForm.order_id,
                    goods_id: this.searchForm.goods_id,
                    muid: this.searchForm.muid,
                    change_type: this.searchForm.change_type,
                    end_add_time: this.searchForm.end_add_time,
                    bg_add_time: this.searchForm.bg_add_time,
                }).then(res => {
                    this.datas = res;
                    this.manageType = res.manageType;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
        }
    }
</script>
